<template>
  <div class="left-content" :key="instanceLKey">
    <StationIntroduction
      class="section-box top-section"
      :overviewSearchInfo="overviewSearchInfo"
      v-if="overviewLoading"
    />
    <GenerationStatistics
      class="section-box middel-section"
      :overviewSearchInfo="overviewSearchInfo"
      v-if="overviewLoading"
    />
    <SocialContribution
      class="section-box bottom-section"
      :overviewSearchInfo="overviewSearchInfo"
      v-if="overviewLoading"
    />
  </div>
  <div class="middle-content" :key="instanceTKey">
    <MiddleIndicator
      icon="gf-capacity"
      label="光伏装机(MW)"
      :value="topIndexData.gfCapacity"
      @click="handleElecIndex"
      vColor="#67C8FF"
    />
    <MiddleIndicator
      icon="cn-capacity"
      label="储能装机(MW)"
      :value="topIndexData.cnCapacity"
      @click="handleElecIndex"
      vColor="#F4D038"
    />
    <MiddleIndicator
      icon="safe-operation"
      label="安全运行(天)"
      :value="topIndexData.runningDays"
      vColor="#00BA24"
    />
  </div>
  <div class="right-content" :key="instanceRKey">
    <RealTimePower
      class="section-box top-section"
      :overviewSearchInfo="overviewSearchInfo"
      v-if="overviewLoading"
    />
    <GenerationEfficiency
      class="section-box middel-section"
      :overviewSearchInfo="overviewSearchInfo"
      v-if="overviewLoading"
    />
    <GenerationTrend
      class="section-box bottom-section"
      :overviewSearchInfo="overviewSearchInfo"
      v-if="overviewLoading"
    />
  </div>
</template>

<script lang="ts" setup>
  import StationIntroduction from './StationIntroduction.vue';
  import GenerationStatistics from './GenerationStatistics.vue';
  import SocialContribution from './SocialContribution.vue';
  import MiddleIndicator from '/@/components/Framework/OperationScreen/MiddleIndicator.vue';
  import RealTimePower from './RealTimePower.vue';
  import GenerationEfficiency from './GenerationEfficiency.vue';
  import GenerationTrend from './GenerationTrend.vue';
  import { reactive, UnwrapRef, onMounted, ref } from 'vue';
  import { getTopInfoData } from './data';
  import { assign } from 'lodash-es';
  import { addTabPage } from '@/utils/route';
  import { buildUUID } from '@/utils/uuid';

  //搜索参数
  const overviewSearchInfo: UnwrapRef<any> = reactive({});
  const topIndexData: UnwrapRef<any> = reactive({});
  const overviewLoading = ref<boolean>(false);
  const instanceLKey = ref(buildUUID());
  const instanceRKey = ref(buildUUID());
  const instanceTKey = ref(buildUUID());

  interface Props {
    searchStationInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});

  // 跳转并网容量统计页面
  function handleElecIndex() {
    addTabPage('/bi/grid/capacity/statistics', '并网容量统计', {
      stationId: props.searchStationInfo.stationUUId,
    });
  }

  // 更新组件函数
  const handleUpdate = () => {
    instanceLKey.value = buildUUID();
    instanceRKey.value = buildUUID();
    instanceTKey.value = buildUUID();
  };

  // 获取顶部指标数据
  const queryTopInfoData = async () => {
    let data = await getTopInfoData(overviewSearchInfo);
    if (data.result) {
      overviewLoading.value = true;
      assign(topIndexData, data.result);
    }
  };

  defineExpose({
    handleUpdate,
  });

  onMounted(() => {
    assign(overviewSearchInfo, props.searchStationInfo);
    // 顶端指标
    queryTopInfoData();
  });
</script>

<style lang="less" scoped>
  .left-content {
    display: flex;
    position: absolute;
    top: 0.9rem;
    left: 0;
    flex-direction: column;
    width: 25vw;
    height: calc(100vh - 0.9rem);
    padding: 0 0.57vw 1.68vh 0.68vw;
  }

  .middle-content {
    display: flex;
    position: absolute;
    top: 0.96rem;
    left: 25vw;
    justify-content: space-between;
    width: 50vw;
  }

  .right-content {
    display: flex;
    position: absolute;
    top: 0.9rem;
    right: 0;
    flex-direction: column;
    width: 25vw;
    height: calc(100vh - 0.9rem);
    padding: 0 0.68vw 1.68vh 0.57vw;
  }

  .section-box {
    flex: 1;
    width: 100%;
    height: 33.3333%;
    overflow: hidden;

    &.top-section {
      padding-bottom: 0.62vh;
    }

    &.middel-section {
      padding: 0.31vh 0;
    }

    &.bottom-section {
      padding-top: 0.62vh;
    }
  }
</style>
